/* Return the first argument given.
 *
 * |Name  |Type|Desc       |
 * |------|----|-----------|
 * |val   |*   |Any value  |
 * |return|*   |Given value|
 */

/* example
 * identity('a'); // -> 'a'
 */

/* module
 * env: all
 * test: all
 */
function exports(val) {
    return val;
}

module.exports = exports;
